.menu{
    width: 300px;
    height: 100vh;
    background-color: rgb(6, 100, 177);
    position: fixed;
    top: 0;
    right: -300px;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: all 1s ease;

    &.active{
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 30px;
        color: white;
        font-weight: 300;
        width: 60%;

        

        li{
            margin-bottom: 20px;
            a{
                font-size: inherit;
                color: inherit;
                text-decoration: none;
            }
           &:hover{
               font-weight: 500;
           }
        }
    }

}